.search-form {
  display: flex;
  justify-content: center;
  padding: 30px;
  margin-bottom: 0px;
  margin-left: 0px;
  background-color: rgb(77, 206, 65);
}

.searchBtn {
  display: flex;
  justify-content: center;
  margin-left: 20px;
  margin-right: auto;
  background-color: hsl(54, 94%, 54%);
}

.button {
  background-color: hsl(54, 94%, 54%);
}

input {
  margin-left: 500px;
}

.btn {
  display: flex;
  justify-content: center;
  background-color: hsl(54, 94%, 54%);
  margin-left: auto;
  margin-right: auto;
}


.btn.is-hidden {
  display: none;
}

.btn > .spinner.is-hidden {
  display: none;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.photo-card {
  margin-left: 20px;
}


